html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  background-color: var(--color-white);
  font-size: 10px;
}

img {
  max-width: 100%;
}

h2 {
  margin: 0;
  padding: 0;
}

.hero-title {
  color: var(--color-black);
  font-family: "Roboto", sans-serif;
  font-size: 64px;
  font-weight: 900;
  text-transform: uppercase;
  font-style: italic;
}

.text {
  color: var(----color-black);
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 21px;
  margin: 0;
}

.strong-text {
  color: var(--color-black);
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 500;
  display: inline-block;
}

.title-middle, .title-middle--center {
  color: var(--color-black);
  font-family: "Roboto", sans-serif;
  font-size: 36px;
  font-weight: 700;
  display: block;
  line-height: 42px;
}

.title-middle--center {
  text-align: center;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  border: none;
}

section {
  position: relative;
}

a {
  display: inline-block;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.center-horizontal {
  display: flex;
  justify-content: space-between;
}

.center-vertical {
  display: flex;
  align-items: center;
}

.logo {
  color: var(--color-black);
  font-family: "Montserrat", sans-serif;
  font-size: 3.125rem;
  font-weight: 900;
  text-transform: uppercase;
}

.h2 {
  color: var(----color-black);
  font-family: "Roboto", sans-serif;
  font-size: 2.25rem;
  font-weight: 700;
  text-align: center;
  margin: 0;
  line-height: 1;
  margin-bottom: 60px;
}

.subtitle {
  color: var(--color-black);
  font-family: Roboto;
  font-size: 1.125rem;
  font-weight: 400;
  font-style: italic;
  text-align: center;
  margin-top: -40px;
  margin-bottom: 51px;
}

.section {
  padding: 60px 0;
}

.btn {
  color: var(--color-white);
  cursor: pointer;
  transition: 0.2s ease;
}

.btn-blue {
  border-radius: 2.188rem;
  background-color: var(--color-blue);
  color: var(--color-white);
}

.btn-red {
  background-color: #ef4949;
}

.btn-red:hover {
  -webkit-box-shadow: -2px 4px 22px 0px #ef4949;
  -moz-box-shadow: -2px 4px 22px 0px #ef4949;
  box-shadow: -2px 4px 22px 0px #ef4949;
}

.btn-blue:hover {
  -webkit-box-shadow: -2px 4px 22px 0px #236eff;
  -moz-box-shadow: -2px 4px 22px 0px #236eff;
  box-shadow: -2px 4px 22px 0px #236eff;
}

.btn-grey {
  border-radius: 1.563rem;
  background-color: #eeeeee;
  color: var(--color-black);
}

.btn-grey:hover {
  -webkit-box-shadow: -2px 4px 22px 0px #eeeeee;
  -moz-box-shadow: -2px 4px 22px 0px #eeeeee;
  box-shadow: -2px 4px 22px 0px #eeeeee;
}

.btn-lg {
  font-family: "Open Sans", sans-serif;
  font-size: 2.25rem;
  font-weight: 700;
}

.btn-header {
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  padding: 10px 21px;
  font-style: italic;
}

.btn-section, .btn-section--center {
  font-family: Roboto;
  font-size: 1.125rem;
  font-weight: 400;
  padding: 13px 60px;
  font-style: italic;
}

.btn-section--center {
  margin: 0 auto;
  display: block;
}

.video .container {
  position: relative;
  padding-bottom: 36.25%;
  /* 16:9 */
  height: 0;
  max-height: 500px;
}

.video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.header {
  padding: 22px 0;
  background-color: var(--color-white);
}

.header.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.header .container {
  padding: 0;
  padding: 0 8px;
  display: flex;
  justify-content: space-between;
}

.header__logo {
  padding-left: 15px;
}

.header__nav {
  display: flex;
  align-items: center;
  flex-grow: 1;
  justify-content: flex-end;
}

.header .nav__list {
  display: flex;
  margin: 0;
  padding: 0;
}

.header .nav__item:not(:last-child) {
  margin-right: 40px;
}

.header .nav__link {
  color: var(----color-black);
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  position: relative;
}

.header .nav__link::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -7px;
  left: 0;
  width: 0;
  height: 3px;
  border-radius: 2px;
  transition: 0.2s ease;
}

.header .nav__link.active {
  cursor: default;
}

.header .nav__link.active::after, .header .nav__link:hover::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -7px;
  left: 0;
  width: 100%;
  height: 3px;
  border-radius: 2px;
  background-color: var(--color-blue);
}

.header__contact {
  display: flex;
  align-items: center;
  flex-grow: 1;
  justify-content: flex-end;
}

.header__contact .btn-header {
  margin-right: 20px;
  letter-spacing: normal;
}

.header .contact__phone {
  display: flex;
}

.header .contact__phone--icon {
  margin-right: 9px;
}

.header .phone__link {
  color: var(----color-black);
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 1;
}

.main-section .main-slider {
  height: calc(100vh - 101px);
}

.main-section .main-slider__btn {
  padding: 11px 95px;
  margin-top: 70px;
}

.main-section .main-slider .arrow__container {
  display: flex;
  flex-direction: row;
  z-index: 1;
  margin-top: 94px;
}

.main-section .main-slider .arrow-left, .main-section .main-slider .arrow-right {
  width: 60px;
  height: 60px;
  border-radius: 15rem;
  background-color: var(--color-white);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.main-section .main-slider .arrow-left:hover, .main-section .main-slider .arrow-right:hover {
  background-color: #7dc4ea;
}

.main-section .main-slider .arrow-left {
  margin-right: 40px;
}

.main-section .slide__container {
  z-index: 0;
  padding-top: 184px;
}

.main-section .slide__title {
  margin-bottom: 61px;
}

.main-section .slide__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.main-section .slide__product {
  font-family: "Montserrat", sans-serif;
  font-size: 4rem;
  font-weight: 300;
  font-style: italic;
}

.main-section .slide__description {
  color: rgba(34, 34, 34, 0.8);
  font-family: "Roboto", sans-serif;
  font-size: 2.25rem;
  font-weight: 300;
  max-width: 570px;
}

.main-section .logo {
  font-size: 4rem;
}

.questions {
  background-color: #eeeeee;
}

.questions__container {
  display: flex;
  flex-wrap: wrap;
}

.questions__item {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex: 1 0 auto;
  filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.05));
}

.questions__image-block {
  max-width: 140px;
  width: 100%;
  align-self: center;
}

.questions__image-block {
  width: 140px;
  height: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  background-color: var(--color-white);
  margin-bottom: 26px;
}

.questions__text {
  color: var(--color-black);
  font-family: Roboto;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  font-style: italic;
  max-width: 160px;
  align-self: center;
}

.install {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background-color: #fafafa;
}

.install__container {
  display: flex;
  flex-direction: row;
}

.install__left {
  width: 63%;
}

.install .hero-title {
  margin-top: 60px;
  margin-bottom: 44px;
}

.install .text {
  max-width: 690px;
  padding-left: 3px;
  margin-bottom: 41px;
}

.install .strong-text {
  padding-left: 3px;
}

.options-section {
  background-color: #fafafa;
}

.options-section .title-middle--center {
  margin-bottom: 60px;
}

.options__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.options__image-block {
  height: 222px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.options__item {
  display: flex;
  height: 499px;
  flex: 1 0 auto;
  flex-direction: column;
  border-radius: 40px;
  background-color: var(--color-white);
  padding: 0 14px;
  max-width: 270px;
  filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.05));
}

.options__item:not(:last-child) {
  margin-right: 20px;
}

.options__title, .options__title--center {
  color: var(--color-black);
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 49px;
}

.options__title--center {
  text-align: center;
}

.options__text, .options__text--center {
  color: var(--color-black);
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 400;
  font-style: italic;
}

.options__text--center {
  text-align: center;
}

.shop-section {
  background-color: #fafafa;
}

.shop-section .container {
  max-width: 1200px;
}

.shop__container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1180px;
  flex-direction: row;
}

.shop__item {
  max-width: 380px;
  width: 100%;
  min-height: 639px;
  display: flex;
  flex-direction: column;
  border-radius: 40px;
  background-color: var(--color-white);
  padding: 39px 40px 40px 40px;
  margin-bottom: 10px;
  filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.05));
  transition: box-shadow 0.2s ease;
  overflow: hidden;
}

.shop__item:hover {
  box-shadow: -2px 4px 22px 0 #236eff;
}

.shop__item .hits, .shop__item .hits--black, .shop__item .hits--red, .shop__item .hits--orange {
  width: 200px;
  height: 37px;
  display: flex;
  align-items: center;
  text-align: center;
  position: absolute;
  top: 20px;
  right: -55px;
  transform: rotate(45deg);
  text-shadow: 0.063rem 0.063rem 0.125rem rgba(0, 0, 0, 0.2);
  color: var(--color-white);
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 0.8rem;
  text-transform: uppercase;
  line-height: 1;
  padding: 0 50px;
}

.shop__item .hits--black {
  background: linear-gradient(180deg, #222222 1.12%, #222222 100.02%);
}

.shop__item .hits--red {
  background-color: var(--color-red);
  padding: 0 67px;
}

.shop__item .hits--orange {
  background-color: #fa5e00;
  padding: 0px 66px;
}

.shop__item:not(:nth-child(3n)) {
  margin-right: 15px;
}

.shop__title {
  color: var(--color-black);
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: 400;
  text-align: center;
}

.shop__image-block {
  height: 263px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shop__features {
  display: flex;
  flex-direction: column;
  padding-left: 35px;
  margin-bottom: auto;
}

.shop__features__item {
  position: relative;
  color: var(--color-black);
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  margin-bottom: 12px;
  line-height: 21px;
  font-style: italic;
}

.shop__features__item::before {
  content: "";
  display: block;
  position: absolute;
  width: 25px;
  height: 25px;
  top: 0;
  left: -40px;
  background-image: url(../img/features-icon.svg);
  background-size: contain;
}

.shop__footer {
  position: relative;
  border-radius: 50px;
  background-color: #eeeeee;
  height: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: flex-end;
  width: 100%;
}

.shop__price, .shop__price--old, .shop__price--new {
  color: var(--color-black);
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  display: flex;
  align-items: center;
  padding-left: 30px;
  line-height: 1;
}

.shop__price--old {
  position: absolute;
  color: #999999;
  top: -28px;
  left: 0;
  font-size: 1.125rem;
  font-weight: 400;
  text-decoration: line-through;
}

.shop__price--new {
  color: var(--color-red);
}

.shop__button {
  display: flex;
  color: var(--color-white);
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  padding: 11px 37px;
  font-style: italic;
}

.call-to-action__section {
  background: linear-gradient(90deg, #0056a8 20.9%, #236eff 100%);
}

.call-to-action__block {
  display: flex;
  flex-direction: row;
}

.call-to-action__left {
  width: 60%;
}

.call-to-action__right {
  display: flex;
  align-items: center;
  flex-grow: 1;
  justify-content: flex-end;
}

.call-to-action__title {
  color: var(--color-white);
  font-family: "Roboto", sans-serif;
  font-size: 2.25rem;
  font-weight: 700;
  margin-bottom: 20px;
}

.call-to-action__text {
  color: var(--color-white);
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  font-style: italic;
}

.call-to-action__button {
