.articles {
  &__section {
    // background-color: #eeeeee;
    .btn-section--center {
      margin-top: 60px;
    }
  }

  &__block {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__item {
    max-width: 380px;
    border-radius: 1.25rem;
    background-color: var(--color-white);
    padding: 20px 19px;
    min-height: 495px;
    filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.05));
    transition: box-shadow 0.2s ease;
    &:hover {
      box-shadow: -2px 4px 22px 0 #236eff;
    }
  }

  &__title {
    color: var(--color-black);
    font-family: Roboto;
    font-size: 1.5rem;
    font-weight: 700;
    min-height: 104px;
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__image-block {
    margin-bottom: 40px;
  }

  &__text {
    color: var(--color-black);
    font-family: Roboto;
    font-size: 1.125rem;
    font-weight: 400;
    font-style: italic;
  }

}
